.calculator-results-section-heading-plans{
    font-weight: 600;
    font-size: 21px;
    color: #344054;
    margin-bottom: 10px;
}

.calculator-results-section-value-plans{
    font-weight: 400;
    font-size: 24px;
    color: #344054;
    margin-top: 5px;
}

.calculator-two-column-layout-plans{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 5px;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 15px;
}

.calculator-two-column-layout-plans div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.calculator-two-column-layout-plans div:last-child{
    grid-column: span 2;
}

.css-k008qs{
    justify-content: center;
}
